@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500;700&display=swap);
:root {
    --primary-green: #314e1c;
    --secondary-green: #365829;
    --tertiary-green: #69ad53;
    --quaternary-green: #7cbc6c;
    --black: #000333;
    --grey: #333;
    --white: #fff;
}

body {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
/* @import url("https://fonts.googleapis.com/css2?family=Signika+Negative:wght@400;600&display=swap"); */

@font-face {
    font-family: RickAndMorty;
    src: url(/static/media/get_schwifty.4b4420cf.ttf) format('ttf');
}

@font-face {
    font-family: RickAndMorty;
    src: url(/static/media/get_schwifty.4b4420cf.ttf);
}

*, html {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.App {
    background-color: var(--grey);
    color: var(--white);
}

.header-text {
    background-color: var(--black) !important;
    color: var(--tertiary-green) !important;
}

.brand {
    font-family: RickAndMorty;
}

.btn, input[type="radio"], .btn:focus, input[type="radio"]:focus {
    box-shadow: 1px 3px 9px rgba(0, 0, 0, 0.25);
    background-color: var(--primary-green);
    border: 2px solid var(--secondary-green);
    color: var(--white);
}

.btn:active, input[type="radio"]:active, .btn:hover, input[type="radio"]:hover {
    outline: none;
    background-color: var(--secondary-green);
    border: 2px solid var(--secondary-green);
    box-shadow: 1px 3px 9px rgba(0, 0, 0, 0.25);
}

.pagination .btn {
    font-family: RickAndMorty;
}

.btn.disabled {
    background-color: var(--primary-green) !important;
    border: 2px solid var(--primary-green) !important;
    color: var(--grey) !important;
}

.nav-link {
    color: var(--quaternary-green) !important;
}

.page-link {
    background-color: var(--tertiary-green) !important;
    color: var(--white) !important;
    border: 3px solid var(--quaternary-green) !important;
    outline: none;
    font-weight: bold;
}

.page-item.active .page-link {
    background-color: var(--primary-green) !important;
    border: 3px solid var(--secondary-green) !important;
}

.page-link:hover {
    background-color: var(--quaternary-green) !important;
}

.nav-link.active {
    color: var(--tertiary-green) !important;
    border-bottom: 3px solid var(--quaternary-green) !important;
    font-weight: bold;
    border-bottom: 3px solid #0b5ed7;
}
.flippable-card {
    position: relative;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transform: perspective(1000px) rotateY(0) translateY(0);
            transform: perspective(1000px) rotateY(0) translateY(0);
    -webkit-transform: perspective(1000px) rotateY(var(--rotate-y, 0)) translateY(var(--translate-y, 0));
            transform: perspective(1000px) rotateY(var(--rotate-y, 0)) translateY(var(--translate-y, 0));
    transition: 150ms;
    cursor: pointer;
    height: 500px;
}

.char-card {
    background-color: var(--black);
    border-color: var(--primary-green);
    box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.3);
    border-width: 3px;
    height: 100%;
    border-radius: 10px;
    color: var(--quaternary-green);
    font-family: "ubuntu" !important;
}

.flippable-card:hover {
    --translate-y: -2px;
}

.flippable-card:hover .char-card {
    box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.5);
}

.img {
    border-radius: 10px 10px 0 0;
}

.badge {
    top: 10px;
    right: 10px;
    font-size: 17px;
}

.flippable-card.flip {
    --rotate-y: 180deg;
}

.char-card.front {
    left: 0;
}

.flippable-card .front,
.flippable-card .back {
    position: absolute;
    padding: 1rem;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}

.flippable-card .back {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    width: 100%;
}
.accordion-button {
    color: var(--white);
}

.accordion-button:not(.collapsed) {
    color: var(--white);
    background-color: var(--primary-green);
}

.accordion-button:not(.collapsed), .accordion-button:focus {
    outline: none;
    box-shadow: none;
}

.accordion-button.collapsed {
    color: var(--white);
    background-color: var(--secondary-green);
    outline: none;
}

.accordion-button::after {
    fill: var(--white);
}

.accordion-body {
    background-color: var(--tertiary-green);
}
.input {
    width: 40%;
    border-radius: 8px;
    border: 2px solid var(--primary-green);
    box-shadow: 1px 3px 9px rgba(0, 0, 0, 0.25);
    padding: 10px 15px;
}

.input:focus {
    outline: none;
}

@media (max-width: 576px) {
    .input {
        width: 80%;
    }
}
